import qs from 'qs';
import { config } from '../config';
import {
  ArchiveActionResult,
  ArchiveExtendedInfo,
  ArchiveObjectContainer,
  ArchiveSimpleInfo,
  ImgCacheElement
} from "../types";

export async function getArchiveImages(archiveId: string, positions: number[]): Promise<ImgCacheElement[]> {
  const queryPositions = qs.stringify({ i: positions }, { indices: false });
  let apiText;
  if (config.PandaBackupKey === '') {
    apiText = ''
  } else {
    apiText = `&api_key=${config.PandaBackupKey}`;
  }
  const query = `${config.PandaBackupUrl}/r-api/archive/${archiveId}/image_list/?${queryPositions}${apiText}`;
  const response = await fetch(query,
    {
      credentials: 'include',
    });
  return await response.json();
}

export async function getArchiveExtendedComponent(archiveId: string, position: number): Promise<ArchiveExtendedInfo> {
  const correctedPosition = position || 1;
  let apiText;
  if (config.PandaBackupKey === '') {
    apiText = ''
  } else {
    apiText = `&api_key=${config.PandaBackupKey}`;
  }
  const query = `${config.PandaBackupUrl}/r-api/archive/${archiveId}/?position=${correctedPosition}${apiText}`;
  const response = await fetch(query,
    {
      credentials: 'include',
    });
  return await response.json();
}

