export async function getArchivesComponent(queryString: string): Promise<ArchiveObjectContainer> {
  let apiText;
  if (queryString === '') {
    apiText = `?api_key=${config.PandaBackupKey}`;
  } else {
    apiText = `&api_key=${config.PandaBackupKey}`;
  }
  const query = `${config.PandaBackupUrl}/r-api/archives/${queryString}${apiText}`;
  const response = await fetch(query,
    {
      credentials: 'include',
    });
  return await response.json();
}

export async function archiveAction(archiveId: string, action: string): Promise<ArchiveActionResult> {
  let apiText;
  if (config.PandaBackupKey === '') {
    apiText = ''
  } else {
    apiText = `&api_key=${config.PandaBackupKey}`;
  }
  const query = `${config.PandaBackupUrl}/r-api/archive/${archiveId}/${action}/?${apiText}`;
  const response = await fetch(query,
    {
      credentials: 'include',
    });
  return await response.json();
}
