import React from 'react';
import { Button } from 'reactstrap';

interface Props {
  onClick: () => void;
  showContent: boolean;
  alignRight: boolean;
}

const commonStyle = {position: 'absolute', top: '0px'};

const ToggleButton = ({ onClick, showContent, alignRight }: Props): JSX.Element => {
  let style;
  const showContentIcon = alignRight ? '>' : '<';
  const hideContentIcon = alignRight ? '<' : '>';
  if(showContent) {
    style = Object.assign({}, commonStyle, alignRight ? {left: '-30px'} : {right: '-30px'})
  } else {
    style = Object.assign({}, commonStyle, alignRight ? {right: '0'} : {left: '0'})
  }
  return (<Button onClick={onClick} size="sm" style={style}>{showContent ? showContentIcon : hideContentIcon }</Button>)
};

interface PaginationProps {
  onClick: () => void;
  showContent: boolean;
  topSkip: number;
  staticContent: string;
}

const commonStylePagination = {position: 'absolute', width: '35px'};

export const PaginationToggleButton = ({ onClick, showContent, topSkip, staticContent }: PaginationProps): JSX.Element => {
  let style;

  if(showContent) {
    style = Object.assign({}, commonStylePagination, {right: '-36px'})
  } else {
    style = Object.assign({}, commonStylePagination, {left: '0'})
  }

  style.top = `${topSkip}px`

  return (<Button color="info" onClick={onClick} size="sm" style={style}>{staticContent}</Button>)
};

export default ToggleButton;