import React from 'react';
import {
  BrowserRouter,
  Route,
} from 'react-router-dom'
import Home from './Home';
import { config } from '../config';

const App = (): JSX.Element => (
  <React.StrictMode>
    <BrowserRouter basename={config.basename}>
      <Route path="/" component={Home}/>
    </BrowserRouter>
  </React.StrictMode>
);

export default App;
