import React from 'react';
import { Progress } from 'reactstrap';
import {ImgCacheElement} from "../../types";

interface Props {
  images: {
    [imgId: number]: ImgCacheElement;
  };
}

const PreloadProgressBars = ({ images }: Props): JSX.Element => (
  <div style={{position: 'absolute', left: '85%', top: '16%', width: '15%'}}>
    {
      Object.keys(images).map(x => {
        const percent = images[Number(x)].percent;
        switch (images[Number(x)].state) {
          case 'progress':
            return <Progress key={x} color={percent < 0 ? "warning" : "success"} value={percent}
            >{`${x}: ${percent}%`}</Progress>;
          case 'load':
            return <Progress key={x} color="info" value={percent}>{`${x}: ${percent}%`}</Progress>;
          case 'error':
            return <Progress key={x} color="danger" value={percent}>{`${x}: ${percent}%`}</Progress>;
          case 'abort':
            return <Progress key={x} color="danger" value={percent}>{`${x}: ${percent}%`}</Progress>;
          default:
            return <Progress key={x} color="info" value={percent}>{`${x}: ${percent}%`}</Progress>;
        }
      })
    }
  </div>
);

export default PreloadProgressBars;
