import React from 'react';
import s from './Help.scss';
import cx from 'classnames';

interface Props {
  actions: {
    key: string;
    name: string;
    description: string;
    action?: () => void
  }[];
}


const Help = ({actions}: Props): JSX.Element => (
  <div className={s.flex}>
    <div className={cx(s.verticalCenter, s.fullWidth)}>Keyboard shortcuts</div>
    {
      actions.map((x) =>
        <div className={cx(s.verticalCenter, s.flex, s.fullWidth)} key={x.key}>
          <div className={cx(s.grow)}>{x.description}</div>
          <div className={cx(s.noGrow)}>{x.key}</div>
        </div>
      )
    }
  </div>
);

export default Help;
