import React from 'react';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';
import Select from 'react-select';
import s from './NavigationPager.scss';
import { OptionType } from 'react-select/src/types'

interface Props {
  className: string;
  currentPosition: number;
  lastPosition: number;
  navSelectOptions: { label: number; value: number }[];
  handleChange: (option: OptionType) => void;
  moveLeft: () => void;
  moveFirst: () => void;
  moveRight: () => void;
  moveLast: () => void;
}

const NavigationPager = ({ className, currentPosition, lastPosition, navSelectOptions, handleChange, moveLeft, moveFirst, moveRight, moveLast }: Props): JSX.Element => (
  <Pagination listClassName={"justify-content-center my-0"}>
    <PaginationItem>
      <PaginationLink onClick={moveFirst}>
        &larr;&larr;
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink onClick={moveLeft}>
        &larr;
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <div className={s.midItem}>
        <Select
          className={className}
          name="select-page"
          value={{value: currentPosition, label: currentPosition}}
          onChange={handleChange}
          options={navSelectOptions}
          isClearable={false}
          blurInputOnSelect
          noOptionsMessage={() => `No valid option`}
        />
        <span style={{ verticalAlign: 'super' }}>{ ` of ${lastPosition} ` }</span>
      </div>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink onClick={moveRight}>
        &rarr;
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink onClick={moveLast}>
        &rarr;&rarr;
      </PaginationLink>
    </PaginationItem>
  </Pagination>
);

export default NavigationPager;
