import React, {useState} from 'react';
import AsyncSelect from 'react-select/async';
import s from "./ArchiveSelect.scss";
import {getArchiveSimpleList} from "../../core/apiCalls";

type HeaderProps = {
  handleQueryChange: (value: string) => void;
}

const customStyles = {
  menu: (provided, state) => ({
    ...provided,
    color: state.selectProps.menuColor,
  }),
}

function ArchiveSelect({ handleQueryChange }: HeaderProps): JSX.Element {
  const [, setValue] = useState('');
  const [selectedValue, setSelectedValue] = useState(null);

  // handle input change event
  const handleInputChange = value => {
    setValue(value);
  };

  // handle selection
  const handleChange = value => {
    setSelectedValue(value);
    handleQueryChange(value.id);
  }

  // load options using API call
  const loadOptions = (inputValue) => {
    return getArchiveSimpleList(inputValue);
  };

  return (
    <div className={s.modalContainer}>
      <AsyncSelect
        cacheOptions
        defaultOptions={[]}
        value={selectedValue}
        getOptionLabel={e => e.title}
        getOptionValue={e => e.id}
        loadOptions={loadOptions}
        onInputChange={handleInputChange}
        onChange={handleChange}
        menuColor='black'
        styles={customStyles}
      />
    </div>
  );
}

export default ArchiveSelect;