/* eslint-disable max-len */
import React, {Component} from 'react';
import { Spinner } from 'reactstrap';
import { GlobalHotKeys } from 'react-hotkeys';
import Slider from 'react-slick';
import Select from 'react-select';
import { withRouter } from 'react-router-dom';
import update from 'immutability-helper';
import cx from 'classnames';
import NavigationPager from './NavigationPager';
import PreloadProgressBars from './PreloadProgressBars';
import s from './ImageViewer.scss';
import {getArchiveImages} from "../../core/apiCalls";
import { OptionType } from 'react-select/src/types'
import {
  ArchiveExtendedInfo,
  GenericHTMLDivElement,
  ImgCacheElement,
} from "../../types";
import {fileSizeIEC} from "../../core/utils";

function imgRight(evt) {
  const img = evt.currentTarget;
  let left = 0;
  let offset = img;
  while (offset) {
    left += offset.offsetLeft;
    offset = offset.offsetParent;
  }
  if (img.style.paddingLeft) left += Number(img.style.paddingLeft.match(/\d+/)[0]);
  left -= window.scrollX;
  const x = evt.clientX - left;
  const w = img.width || img.offsetWidth;
  return x / w > 0.5;
}

function getRelativeImages(current: number, start: number, end: number, length: number): [number[], number[]] {
  let startRange = current - length;
  let endRange = current + length;
  if (startRange < start) {
    startRange = start;
  }
  if (endRange > end) {
    endRange = end;
  }
  return [
    new Array(current - startRange).fill(0).map((_, i) => current - i - 1),
    new Array(endRange - current).fill(0).map((_, i) => i + current + 1),
  ];
}

function preloadOrder(current: number, arrays: [number[], number[]]) {

  const length = arrays[0].length + arrays[1].length;
  const result = [];

  for (let itemIndex = 0; itemIndex < length; itemIndex++) {
    for (let arrayIndex = 0; arrayIndex < 2; arrayIndex++) {
      if (arrays[arrayIndex].length - 1 < itemIndex) {
        continue;
      }
      result.push(arrays[arrayIndex][itemIndex]);
    }
  }

  return [current, ...result]
}

function calculateWidthHeight(width: number, height: number, is_horizontal: boolean, horizontal: number, vertical: number): [number, number] {
  let startWidth: number;
  let startHeight: number;
  if (is_horizontal) {
    if (width >= horizontal) {
      startWidth = horizontal;
      startHeight = (startWidth * height) / width;
    } else {
      startWidth = width;
      startHeight = height;
    }
  } else if (width >= vertical) {
    startWidth = vertical;
    startHeight = (startWidth * height) / width;
  } else {
    startWidth = width;
    startHeight = height;
  }
  return [startWidth, startHeight];
}

interface Props {
  archive: ArchiveExtendedInfo;
  startIndex: number;
  startPosition: number;
  location: Location;
  history: {
    push: (url: string) => void;
    replace: (url: string) => void;
  };
  handleQueueChange: (archiveId: string, queueChange: number) => void;
  displayCovers: boolean;
  displayNavigators: boolean;
  displayMetadata: boolean;
  displayPreloadInfo: boolean;
  doublePageMode: boolean;
  setCurrentContainer: (container: GenericHTMLDivElement | null) => void;
  autoWidth: boolean;
  scale: number;
  preloadCount: number;
  displayAllPreloaded: boolean;
}

interface State {
  currentArchiveId: string;
  currentPosition: number;
  positionModal: number;
  horizontal: number;
  vertical: number;
  showPositionModal: boolean;
  loading: boolean;
  navImages: [number[], number[]];
  images: {
    [imgId: number]: ImgCacheElement;
  };
}

const keyMap = {
  moveLeft: ['left', 'a'],
  moveRight: ['right', 'd'],
  moveFirst: 'shift+left',
  moveLast: 'shift+right',
  previousQueue: 'y',
  nextQueue: 'i',
  togglePositionModal: 'u'
};

class ImageViewer extends Component<Props, State> {

  node: HTMLDivElement | null;
  imgContainer: GenericHTMLDivElement | null;
  modalInput: { current: null | Select };
  slider: Slider;

  constructor(props: Props) {
    super(props);
    this.modalInput = React.createRef();

    const images = {};

    if(props.archive.startImage) {
      const [startWidth, startHeight] = calculateWidthHeight(
        props.archive.startImage.width,
        props.archive.startImage.height,
        props.archive.startImage.is_horizontal,
        1500,
        900,
      );
      if(props.archive.startImage) {
        images[props.startIndex] = {
          position: props.startIndex,
          url: props.archive.startImage.url,
          width: startWidth,
          height: startHeight,
          is_horizontal: props.archive.startImage.is_horizontal,
          state: 'load',
          percent: 100,
        };
      }
    }

    this.state = {
      currentArchiveId: props.archive.id,
      currentPosition: props.startIndex,
      positionModal: props.startIndex,
      horizontal: 1500,
      vertical: 900,
      showPositionModal: false,
      loading: true,
      navImages: getRelativeImages(props.startIndex, props.startPosition, props.archive.lastPosition, props.preloadCount),
      images: images,
    };

  }

  componentDidMount() {
    // window.addEventListener('keydown', this.handleKeyDown);
    // updateByIndexIfQueued(this.context.dispatcher, this.props.archive.id, this.state.currentPosition);
    this.getNonCachedImagesUrls(this.props.archive.id, preloadOrder(this.state.currentPosition, [this.state.navImages[1], this.state.navImages[0]]));
    if (this.node)
      this.node.scrollIntoView();
    if (this.props.displayCovers) {
      this.slider.slickGoTo(this.state.currentPosition - 1);
    }
    this.props.setCurrentContainer(this.imgContainer);
  }

  static getDerivedStateFromProps(props: Props, state: State) {
    const newId = props.archive.id;
    const oldId = state.currentArchiveId;
    const newImgId = props.startIndex;
    const oldImgId = state.currentPosition;
    if (newId !== oldId) {
      const images = {};
      if(props.archive.startImage) {
        const [startWidth, startHeight] = calculateWidthHeight(
          props.archive.startImage.width,
          props.archive.startImage.height,
          props.archive.startImage.is_horizontal,
          state.horizontal,
          state.vertical,
        );
        if(props.archive.startImage) {
          images[props.startIndex] = {
            position: props.startIndex,
            url: props.archive.startImage.url,
            width: startWidth,
            height: startHeight,
            is_horizontal: props.archive.startImage.is_horizontal,
            state: 'load',
            percent: 100,
          };
        }
      }

      return {
        currentArchiveId: newId,
        currentPosition: props.startIndex,
        positionModal: props.startIndex,
        loading: true,
        navImages: getRelativeImages(props.startIndex, props.startPosition, props.archive.lastPosition, props.preloadCount),
        images: images,
      };
    } else if (newImgId !== oldImgId) {
      return {
        loading: true,
        navImages: getRelativeImages(newImgId, props.startPosition, props.archive.lastPosition, props.preloadCount),
        currentPosition: newImgId,
        positionModal: newImgId,
      };
    }

    return null;

  }

  componentDidUpdate(prevProps: Props){
    const newId = this.props.archive.id;
    const oldId = prevProps.archive.id;
    const newImgId = this.props.startIndex;
    const oldImgId = prevProps.startIndex;
    if (newId !== oldId || newImgId !== oldImgId) {
      // updateByIndexIfQueued(this.context.dispatcher, newId, this.state.currentPosition);
      this.getNonCachedImagesUrls(newId, preloadOrder(this.state.currentPosition, [this.state.navImages[1], this.state.navImages[0]]));
      if(this.imgContainer)
        this.imgContainer.scrollIntoView();
    }
    else if(this.props.displayAllPreloaded != prevProps.displayAllPreloaded) {
      if(this.imgContainer)
        this.imgContainer.scrollIntoView();
    }
    if (this.props.displayCovers) {
      this.slider.slickGoTo(this.state.currentPosition - 1);
    }
  }

  componentWillUnmount() {
    // window.removeEventListener('keydown', this.handleKeyDown);
    this.props.setCurrentContainer(null);
  }

  updateProgress = (oEvent: ProgressEvent<EventTarget>) => {
    const imageTarget = oEvent.currentTarget as HTMLImageElement
    if (oEvent.lengthComputable) {
      this.setState({
        images: update(this.state.images, {[Number(imageTarget.id)]: {$merge: {
          percent: oEvent.loaded / oEvent.total,
        }}})
      });
    } else {
      this.setState({
        images: update(this.state.images, {[Number(imageTarget.id)]: {$merge: {
          percent: -1,
        }}})
      });
    }
  }

  transferComplete = (oEvent: ProgressEvent<EventTarget>) => {
    const imageTarget = oEvent.currentTarget as HTMLImageElement
    this.setState({
      images: update(this.state.images, {[Number(imageTarget.id)]: {$merge: {
        percent: 100,
        state: 'load',
      }}})
    });
  };

  transferFailed = (oEvent: ProgressEvent<EventTarget>) => {
    const imageTarget = oEvent.currentTarget as HTMLImageElement
    this.setState({
      images: update(this.state.images, {[Number(imageTarget.id)]: {$merge: {
        state: 'error',
      }}})
    });
  };

  transferCanceled = (oEvent: ProgressEvent<EventTarget>) => {
    const imageTarget = oEvent.currentTarget as HTMLImageElement
    this.setState({
      images: update(this.state.images, {[Number(imageTarget.id)]: {$merge: {
        state: 'abort',
      }}})
    });
  };

  getImagesData  = (imageList: ImgCacheElement[]) => {
    imageList.filter(x => !{}.hasOwnProperty.call(this.state.images, x.position)).forEach((img) => {
      const fetchImage = new window.Image();
      fetchImage.id = img.position.toString();
      fetchImage.addEventListener("progress", this.updateProgress);
      fetchImage.addEventListener("load", this.transferComplete);
      fetchImage.addEventListener("error", this.transferFailed);
      fetchImage.addEventListener("abort", this.transferCanceled);
      fetchImage.src = img.url;
      // const idNum = Number(id);
      this.setState({
        images: update(this.state.images, {[img.position]: {$set: {
          position: img.position,
          url: img.url,
          width: img.width,
          height: img.height,
          is_horizontal: img.is_horizontal,
          state: 'progress',
          percent: 0,
        }}})
      });
    });
  };


  getNonCachedImagesUrls = (archiveId, positions) => {
    const filteredPositions = positions.filter(x => !{}.hasOwnProperty.call(this.state.images, x));
    if (filteredPositions.length !== 0) {
      getArchiveImages(archiveId, filteredPositions)
        .then(archiveImages => this.getImagesData(archiveImages))
    }
  };

  goToPosition = (position: number) => {
    if(position !== this.state.currentPosition) {
      this.props.history.replace(`/${this.props.archive.id}/img/${position}${this.props.location.search}`);
    }
  };

  handleImgClick = (evt) => {
    imgRight(evt) ? this.moveRight() : this.moveLeft();
  };

  handleSliderChange = (nextSlide: number) => {
    this.goToPosition(nextSlide + 1);
  };

  moveLeft = () => {
    if (this.props.doublePageMode) {
      if (this.state.navImages[0].length > 1) {
        this.handlePositionChanges(this.state.navImages[0][1]);
        return;
      }
    }
    if (this.state.navImages[0].length > 0) {
      this.handlePositionChanges(this.state.navImages[0][0]);
    }
  };

  moveRight = () => {
    if (this.props.doublePageMode) {
      if (this.state.navImages[1].length > 1) {
        this.handlePositionChanges(this.state.navImages[1][1]);
        return;
      }
    }
    if (this.state.navImages[1].length > 0) {
      this.handlePositionChanges(this.state.navImages[1][0]);
    }
  };

  moveFirst = () => {
    this.handlePositionChanges(this.props.startPosition);
  };

  moveLast = () => {
    this.handlePositionChanges(this.props.archive.lastPosition);
  };

  handlePositionModalGoTo = (option: OptionType) => {
    this.handlePositionChanges(option.value);
  };

  handlePositionChanges = (position) => {
    this.goToPosition(position);
    if (this.props.displayCovers) {
      this.slider.slickGoTo(position - 1);
    }
  };

  togglePositionModal = () => {
    this.setState({ showPositionModal: !this.state.showPositionModal });
    if (this.state.showPositionModal && this.modalInput && this.modalInput.current) {
      this.modalInput.current.focus();
    }
    if (!this.state.showPositionModal && this.modalInput && this.modalInput.current) {
      this.modalInput.current.blur();
    }
  };

  correctWith = (image: ImgCacheElement) => {
    if (image.is_horizontal) {
      if (image.width >= this.state.horizontal) {
        return this.state.horizontal;
      }
      return image.width;
    }
    if (image.width >= this.state.vertical) {
      return this.state.vertical;
    }
    return image.width;
  };

  getDisplayImage = (image: ImgCacheElement, autoWidth: boolean, imgStyle: {transform: string; transformOrigin: string}) => (
    <img
      key={image.url}
      className={s.active}
      src={image.url}
      alt=""
      width={autoWidth ? 'auto' : this.correctWith(image)}
      height={image.height}
      style={imgStyle}
    />
  );

  keyHandlers = {
    'moveLeft': this.moveLeft,
    'moveRight': this.moveRight,
    'moveFirst': this.moveFirst,
    'moveLast': this.moveLast,
    'previousQueue': () => this.props.handleQueueChange(this.props.archive.id, -1),
    'nextQueue': () => this.props.handleQueueChange(this.props.archive.id, 1),
    'togglePositionModal': (event: KeyboardEvent) => {this.togglePositionModal(); event.preventDefault()},
  };

  render() {
    const {
      showPositionModal,
      currentPosition, images,
      navImages
    } = this.state;
    const {
      displayCovers, displayNavigators,
      displayMetadata, autoWidth,
      displayPreloadInfo, doublePageMode,
      displayAllPreloaded, scale
    } = this.props;
    const { lastPosition } = this.props.archive;

    const Metadata = displayMetadata && (
      <div className={cx(s.overlay, s.overlayLeft)}>
        <h6 className={s.tag}>{this.props.archive.title}</h6>
        <div className={s.tag}>
          {`Pages: ${this.props.archive.filecount} | Size: ${fileSizeIEC(this.props.archive.filesize)} | `}
          <a href={`${this.props.archive.url}`}>Source</a>
        </div>
        {
          this.props.archive.tags.map(tag => (
            <div className={s.tag} key={tag}>{`${tag}`}</div>
          ))
        }
      </div>
    );
    const imgStyle = {
      // height: 'auto',
      // maxWidth: '100%',
      transform: `scaleX(${scale}) scaleY(${scale})`,
      transformOrigin: 'center top',
    };
    const renderedImages = [];
    let renderedImagesPrevious = [];
    let renderedImagesNext = [];

    if(displayAllPreloaded) {
      renderedImagesPrevious = navImages[0].filter(y => images[y] !== undefined).reverse().map(x =>
        <div key={x}>
          { this.getDisplayImage(images[x], autoWidth, imgStyle) }
        </div>
      );
      renderedImagesNext = navImages[1].filter(y => (images[y] !== undefined) && (!doublePageMode || y != currentPosition+1)).map(x =>
        <div key={x}>
          { this.getDisplayImage(images[x], autoWidth, imgStyle) }
        </div>
      );
    }

    doublePageMode && (currentPosition < lastPosition) && images[currentPosition+1] && renderedImages.push(
      this.getDisplayImage(images[currentPosition+1], autoWidth, imgStyle)
    );
    images[currentPosition] && renderedImages.push(
      this.getDisplayImage(images[currentPosition], autoWidth, imgStyle)
    );

    const thumbnails = displayCovers && new Array(lastPosition).fill(0).map(
      (_, i) => (
        ({}.hasOwnProperty.call(images, (i + 1))) ? (
          <div
            key={images[(i + 1)].url}
            style={{ width: 210, height: 285 }}
          >
            <img
              className={'img-fluid mx-auto'}
              src={images[(i + 1)].url}
              alt=""
              width={200}
              height={'auto'}
            />
          </div>) : (
          <div
            key={String(i + 1)}
            style={{ width: 210, height: 285 }}
          >
            <h3>{'Not loaded'}</h3>
          </div>
        )
      ),
    );
    const navSelectOptions = new Array(lastPosition).fill(0).map(
      (_, i) => ({ value: (i + 1), label: (i + 1) }),
    );
    const ChangePageModal = showPositionModal && (
      <div className={s.modalContainer}>
        <Select
          className={s.inlineBlock}
          name="select-page-fast"
          value={{value: currentPosition, label: currentPosition}}
          onChange={this.handlePositionModalGoTo}
          options={navSelectOptions}
          clearable={false}
          autoBlur
          openOnFocus
          onClose={this.togglePositionModal}
          noResultsText="No valid page"
          ref={ this.modalInput }
        />
      </div>
    );
    const ImagePreLoadIndicator = displayPreloadInfo && <PreloadProgressBars images={images}/>;
    return (
      <GlobalHotKeys className={s.HotKeys} keyMap={keyMap} handlers={this.keyHandlers}>
        <div ref={node => (this.node = node)}>
          <h6>{this.props.archive.title}</h6>
          {
            displayCovers &&
            <Slider
              dots={false}
              infinite={false}
              speed={0}
              slidesToShow={3}
              slidesToScroll={1}
              focusOnSelect
              centerMode
              centerPadding={'0'}
              ref={(c) => { this.slider = c; }}
              variableWidth
              initialSlide={this.props.startIndex - 1}
              afterChange={this.handleSliderChange}
            >
              {thumbnails}
            </Slider>}
          {
            displayNavigators &&
              <NavigationPager
                className={s.inlineBlock}
                currentPosition={currentPosition}
                lastPosition={lastPosition}
                moveFirst={this.moveFirst}
                moveLeft={this.moveLeft}
                moveRight={this.moveRight}
                moveLast={this.moveLast}
                handleChange={this.handlePositionModalGoTo}
                navSelectOptions={navSelectOptions}
              />
          }
          { ImagePreLoadIndicator }
          {
            renderedImagesPrevious ?
              <div id="img-previous" className={s.imgContainer}>
                { renderedImagesPrevious }
              </div> :
              null
          }
          <div id="img-display" className={s.imgContainer} onClick={this.handleImgClick} ref={node => (this.imgContainer = node as GenericHTMLDivElement)}>
            { ChangePageModal }
            { images[currentPosition] ? renderedImages : <div>
              <h5>Current image is loading...</h5>
              <Spinner style={{ width: '3rem', height: '3rem' }} color="primary" />
            </div> }
            <div className={cx(s.overlay, s.overlayTop, s.hide)}><span id="img-counter">{currentPosition}</span>/{lastPosition}</div>
            { Metadata }
          </div>
          {
            renderedImagesNext ?
              <div id="img-next" className={s.imgContainer}>
                { renderedImagesNext }
              </div> :
              null
          }
        </div>
      </GlobalHotKeys>
    );
  }

}

export default withRouter(ImageViewer);
