/* eslint no-bitwise: ["error", { "int32Hint": true }] */

export function fileSizeIEC(bytes: number): string {
  const exp = Math.log(bytes) / Math.log(1024) | 0;
  const result = (bytes / (1024 ** exp)).toFixed(2);

  return `${result} ${(exp === 0 ? 'bytes' : `${'KMGTPEZY'[exp - 1]}B`)}`;
}
// KiB,MiB,GiB,TiB,PiB,EiB,ZiB,YiB

const scopePriorities = ['language', 'artist', 'group', 'parody', 'character', 'female', 'male', 'mixed', 'other', 'misc'];

type sortedTagListType = [
  string,
  string[]
]

export function sortTags(tagList: string[]): sortedTagListType[] {
  const prioritizedTaglist = [];

  scopePriorities.forEach((x) => {
    const matchedTags: string[] = tagList.filter(y => y.startsWith(`${x}:`)).sort();
    if (matchedTags.length > 0) {
      prioritizedTaglist.push([x, matchedTags]);
    }
  });
  const remainingTags: string[] = tagList.filter(y => !scopePriorities.find(z => y.startsWith(`${z}:`))).sort();
  if (remainingTags.length > 0) {
    prioritizedTaglist.push(['', remainingTags]);
  }

  return prioritizedTaglist;
}
