import React from 'react';
import cx from 'classnames';
import ArchiveCover from '../ArchiveCover/ArchiveCover';
import ToggleButton, {PaginationToggleButton} from '../ToggleButton';
import s from './SidebarArchives.scss';
import {ArchiveObjectContainer} from "../../types";


interface Props {
  archives: ArchiveObjectContainer;
  displayQueue: boolean;
  toggleQueue: () => void;
  className?: string;
  handleGoToPage: (archivePage: number) => void;
}

const buttonSize = 32;

const SidebarArchives = ({displayQueue, archives, toggleQueue, className, handleGoToPage}: Props): JSX.Element => (
  <div className={cx(className, displayQueue ? s.archCont : null)}>
    <div className={s.overflowScroll}>
      {
        displayQueue ? archives.objects.map(
          (archive) => (
            <div key={archive.id}>
              <ArchiveCover
                archive={archive}
              />
            </div>
          ),
        ) : null
      }
    </div>
    <div>
      <ToggleButton onClick={toggleQueue} showContent={displayQueue} alignRight={false}/>
      {
        displayQueue ?
          <div>
            <PaginationToggleButton
              onClick={() => handleGoToPage(1)}
              showContent={displayQueue}
              staticContent={''}
              topSkip={buttonSize}
            />
            <PaginationToggleButton
              onClick={() => handleGoToPage(archives.has_previous ? archives.number - 1 : 1)}
              showContent={displayQueue}
              staticContent={''}
              topSkip={buttonSize * 2}
            />
            <PaginationToggleButton
              onClick={() => handleGoToPage(archives.has_next ? archives.number + 1 : archives.number)}
              showContent={displayQueue}
              staticContent={''}
              topSkip={buttonSize * 3}
            />
            <PaginationToggleButton
              onClick={() => handleGoToPage(archives.num_pages)}
              showContent={displayQueue}
              staticContent={''}
              topSkip={buttonSize * 4}
            />
          </div> : null
      }
    </div>
  </div>
);

export default SidebarArchives;