import React, { Component } from 'react';
import { Spinner, Button } from 'reactstrap';
import ImageViewer from './ImageViewer/ImageViewer';
import {archiveAction, getArchiveExtendedComponent} from '../core/apiCalls';
import s from './Archive.scss';
import {ArchiveExtendedInfo, GenericHTMLDivElement} from "../types";

interface Props {
  match: {
    params: {
      archiveId: string;
      imgId: string;
    };
  };
  handleQueueChange: (archiveId: string, queueChange: number) => void;
  handleForceUpdateArchives: () => void;
  createLog: (text: string, level: string) => void;
  displayCovers: boolean;
  displayNavigators: boolean;
  displayMetadata: boolean;
  displayPreloadInfo: boolean;
  doublePageMode: boolean;
  displayAllPreloaded: boolean;
  autoWidth: boolean;
  scale: number;
  preloadCount: number;
  setCurrentContainer: (container: GenericHTMLDivElement | null) => void;
}

interface State {
  archive: ArchiveExtendedInfo | null;
  runningAction: boolean;
}

class Archive extends Component<Props, State> {

  state = {
    archive: null,
    runningAction: false,
  };

  componentDidMount(): void {
    getArchiveExtendedComponent(this.props.match.params.archiveId, Number(this.props.match.params.imgId)).then(archive => {
      this.setState({ archive });
      document.title = archive.title;
    })
  }

  componentDidUpdate(prevProps: Props): void {
    const oldId = prevProps.match.params.archiveId;
    const newId = this.props.match.params.archiveId;
    const imgId = this.props.match.params.imgId;
    if (newId !== oldId)
      getArchiveExtendedComponent(newId, Number(imgId)).then(archive => {
        this.setState({ archive });
        document.title = archive.title;
      })
  }

  onExtractClick = (): void => {
    if (!this.state.runningAction && this.state.archive && !this.state.archive.extracted) {
      this.handleArchiveAction(this.state.archive.id, 'extract_archive')
    }
  }

  handleArchiveAction = (archiveId: string, action: string): void => {
    this.setState({ runningAction: true });
    archiveAction(archiveId, action)
      .then((response) => {
        if (response.result === 'ok') {
          const { archive } = this.state;
          if (response.change.field === 'extracted') {
            archive[response.change.field] = (response.change.value.toString() === 'true');
            this.setState({ archive });
          }
          this.props.createLog(`Archive ${archiveId} executed action ${action} successfully`, 'normal');
        } else {
          this.props.createLog(`Archive ${archiveId} executed action ${action} with response ${response.message}: ${response.result}`, 'normal');
        }
        this.setState({ runningAction: false });
        this.props.handleForceUpdateArchives();
      })
      .catch(error => this.props.createLog(`Archive ${archiveId} executed action ${action} with async error: ${error}`, 'normal'));
  };

  render(): JSX.Element {
    const { archive, runningAction } = this.state;
    const {
      match, handleQueueChange, displayCovers,
      displayNavigators, displayMetadata, autoWidth,
      displayPreloadInfo, doublePageMode, setCurrentContainer,
      scale, preloadCount, displayAllPreloaded
    } = this.props;
    return (
      <div className={s.container}>
        {archive ?
          archive.extracted ? <ImageViewer
            startIndex={Number(match.params.imgId)}
            archive={archive}
            startPosition={1}
            handleQueueChange={handleQueueChange}
            displayCovers={displayCovers}
            displayNavigators={displayNavigators}
            displayMetadata={displayMetadata}
            displayPreloadInfo={displayPreloadInfo}
            doublePageMode={doublePageMode}
            displayAllPreloaded={displayAllPreloaded}
            setCurrentContainer={setCurrentContainer}
            autoWidth={autoWidth}
            scale={scale}
            preloadCount={preloadCount}
          /> : <div>
            <h6>{archive.title}</h6>
            <h6>Archive needs to be extracted before using it in here.</h6>
            <Button onClick={this.onExtractClick} size="sm" disabled={runningAction}>{'Extract Archive'}</Button>
          </div>
          : <Spinner className={s.titleContainer} style={{ width: '3rem', height: '3rem' }} color="primary" />
        }
      </div>
    );
  }

}

export default Archive;
