import React from 'react';
import { Button } from 'reactstrap';
import ToggleButton from '../ToggleButton';

interface Props {
  displayControls: boolean;
  toggleControls: () => void;
  className?: string;
  controls: {key: string; name: string; description: string; action?: () => void}[];
}

const SidebarControls = ({displayControls, toggleControls, className, controls}: Props): JSX.Element => (
  <div className={className}>
    {
      <div>
        {
          displayControls ? controls.map(
            (control) => (
              <div key={control.key}>
                <Button onClick={control.action} color="primary" size="sm" block>{control.description}</Button>
              </div>
            ),
          ) : null
        }
        <ToggleButton onClick={toggleControls} showContent={displayControls} alignRight={true}/>
      </div>
    }
  </div>
);

export default SidebarControls;